import React, { Component } from 'react';
import {fundCampaign} from  "../../../action/payAction"
import {connect} from 'react-redux'
import store from '../../../store'
import { 
    withRouter,
   } from "react-router-dom"

   
   import { Redirect, Route } from 'react-router-dom';

import Fragment from "../../../hoc/Fragment"
import LoginModal from "./Modal/LoginModal"


class Fund extends Component {
   
        state ={
            show1: false,
            show2:false
        }
    
    showModal = e => {
        // e.preventDefault()
        this.setState({ show1: true });
        console.log("hello")
        console.log(this.state.show1)
      };

    hideModal = () => {
        this.setState({ show1: false });
    };
    handleSubmit = (e)=> {
        e.preventDefault()
        const {token} = this.props.auth
        if(token===null){
            console.log("User  NOT Logged In")
            this.props.history.push(`/campaigns/${this.props.match.params.id}/Login`)
            // this.showModal();
            // this.setState({ show1: true });
            // console.log(this.state.show1)
            // window.location.href = "."
        } else {
            console.log("User Logged In")
            const {user} = this.props.auth
            const detail = {
                amount: this.amount.value,
                email:this.email.value,
                full_name:this.full_name.value,
                method:user.class,
                userID: user.id,
                projectID: this.props.match.params.id
            }
            console.log(detail)
              this.props.fundCampaign(detail)

        }
     
    }
    render() {
        return (
            <Fragment>
            
            <form 
            action=""
            onSubmit ={this.handleSubmit}
            >
            <div className="form-group">
                <div className="input-group mb-3">
                    <input 
                        required
                        type="text" 
                        className="form-control" 
                        placeholder='Enter Full Name'
                        ref = {(input)=> {this.full_name=input}}
                        />
                </div>
            </div>
            <div className="form-group">
                <div className="input-group mb-3">
                    <input 
                    required
                        type="text" 
                        className="form-control" 
                        placeholder='Enter Email'
                        ref = {(input)=> {this.email=input}}
                    />
                </div>
            </div>

            <div className="form-group">
                <div className="input-group mb-3">
                    <input 
                    required
                        type="text" 
                        className="form-control" 
                        placeholder='Enter Amount'
                        ref = {(input)=> {this.amount=input}}
                        />
                </div>
            </div>
            <button 
                    className="button rounded-0 primary-bg text-white w-100 btn_1"
                    id="exampleModal"
                    type="buttsubmiton"
                
                    >Pay</button>
        </form>
        <LoginModal
                show={this.state.show1}
                handleClose={this.hideModal}
            />
            
        </Fragment>
        );
    }
}


const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaign:state.campaign,
    pay:state.pay
  });
  // export default ProjectForm
  export default connect(mapStateToProps,{fundCampaign})(
    withRouter(Fund)
  );