import {
    USER_LOADING, 
    USER_LOADED, 
    AUTH_ERROR,
    LOGIN_SUCCESS, 
    LOGIN_FAIL,
    LOGOUT_SUCCESS, 
    REGISTER_SUCCESS, 
    REGISTER_FAIL,
    USERS_LOADING, 
    USERS_LOADED,
    UPDATED_USER,
    UPDATING_USER,
    PROFILE_LOADED
 } from "../action/types"
 import axios from "axios"

 const initialState ={
     token:localStorage.getItem("token"),
     isAuthenticated:null,
     isLoading:false,
     user:[],
     profile:[]
 }



 export default function(state =initialState, action){
    
    switch (action.type) {
        
        case USER_LOADED:
            return{
                ...state,
                isAuthenticated:true, 
                isLoading:false,
                user:action.payload,
            }


                
        case USER_LOADING:
            return {
                ...state, 
                isLoading:true
            };

        case LOGIN_SUCCESS:
        case REGISTER_SUCCESS:
            localStorage.setItem("token", action.payload.token)
            console.log("logged in successfully")
        
            return{
                ...state,
                isAuthenticated:true, 
                isLoading:false,
                user:action.payload
            }


            case LOGOUT_SUCCESS:
                localStorage.removeItem("token")
                console.log("logged OUT successfully")
                return{
                    ...state,
                    token:null, 
                    user:null,
                    isAuthenticated:false,
                    isLoading:false
                }
            // if(action.payload.user.user){
            //     console.log("USER", action.payload.user.user._id)
            //     const referredID = action.payload.user.user._id;
            //     var email = action.payload.user.user[action.payload.user.user.method].email
            //     console.log("MY concept to get email ", email);

            //     const config = {
            //         headers: {
            //           // 'Accept': 'application/json',
            //           "Content-Type": "application/json"
            //         }
            //       };

            // }
        //     break;
            
        case REGISTER_FAIL:
            case LOGOUT_SUCCESS :
            localStorage.removeItem("token")
            return{
                ...state,
                token:null, 
                user:null,
                isAuthenticated:false,
                isLoading:false
            }
        
        case USERS_LOADING:
            return{
                ...state,
                isLoading:true
            }
        case USERS_LOADED:
            return{
                ...state,
                isLoading:false,
                user:action.payload
            }


            case UPDATING_USER:
                return{
                    ...state,
                    isLoading:true
                }

                case UPDATED_USER:
                    return{
                        ...state,
                        isLoading:false,
                        user:action.payload
                    }
        default:
            return state;
    }
 }
