// 

import {
    USER_LOADING, 
    PROFILE_LOADED,
    ORGANISATION_PROFILE_LOADED
 } from "../action/types"
 import axios from "axios"

 const initialState ={
     isLoading:false,
     user:[],
     profile:[],
     organisation:[]
 }



 export default function(state =initialState, action){
    
    switch (action.type) {
        
            case ORGANISATION_PROFILE_LOADED:
                return{
                    ...state,
                    isLoading:false,
                    organisation:action.payload
                }
        case USER_LOADING:
            return {
                ...state, 
                isLoading:true
            };

            
        default:
            return state;
    }
 }