export const createCampaign = (fd) => dispatch => {
  
  const config = {
    headers: {
      // 'Accept': 'application/json',
      'Content-Type': 'multipart/form-data',
    }
  };


  axios
    .post(
      "/api/projects/create-campaign",
    fd,
      config
    )
    .then(res =>{
      console.log(res)
      dispatch({
        type: CAMPAIGN_CREATED,
        payload: res.data
      })
      // window.location.href="/dashboard"
    })
    .catch(err => {
      // dispatch(
      //   returnErrors(err, err, "CREATE CAMPAIGN FAILED")
      // );
      console.log("error ", err)
      // window.location.href="/dashboard"
      // console.log(err);
      // dispatch({
      //   type: CREATE_CAMPAIGN_FAILED
      // });
    });
    
};


// https://eles-api.herokuapp.com/api/projects/campaign/update/5e8e40745f28e4001722a4db




export const updateCampaign = (fd, id) => dispatch => {  
  const config = {
    headers: {
      // 'Accept': 'application/json',
      'Content-Type': 'multipart/form-data',
    }
  };


  axios
    .put(
      `/api/projects/campaign/update/${id}`,
    fd,
      config
    )
    .then(res =>{
      dispatch({
        type: CAMPAIGN_UPDATED,
        payload: res.data
      })
      // console.log("RESPONSE", res.data)

      window.location.href="/dashboard"
    })
    .catch(err => {
      dispatch(
        returnErrors(err.response.data, err.response.status, "CREATE CAMPAIGN FAILED")
      );
      console.log(err.response.data);
      dispatch({
        type: CREATE_CAMPAIGN_FAILED
      });
    });
};







