        var newDeadline=new Date(this.project_deadline.value)
        newDeadline= newDeadline.getTime()
        const fd= new FormData()
            // console.log("IMAGE SLIDE", this.image_slide_1.files[0].name)
              fd.append("image_slide_1", this.image_slide_1.files[0], this.image_slide_1.files[0].name)
