import {
    // PAYMENT_COMPLETED,
    // PAY_FAILED,
    PAYMENT_LOADING,
    PAYMENT_VERIFIED

} from "./types"

import axios from 'axios';
import { returnErrors } from './errorActions';


// "amount", 
// "email", 
// "",
// "method", 
// "", 
// ""

var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}

export const fundCampaign = ({amount,email,full_name,method, userID,projectID}) => dispatch => {
    // dispatch({
    //   type:  GETTING_ALL_CAMPAIGN
    // });

    const config ={
      headers:{
          'Content-Type':'application/json'
      }
  };

    const body = JSON.stringify({amount,email,full_name,method, userID,projectID})

    console.log(body)

    axios
      .post(`/api/paystack/pay`, body, config)
      .then(res =>{

        dispatch({
        type: PAYMENT_LOADING,
        payload: res.data
        })
        console.log("PAYMENT DATA",res.data['response'])
        window.location.href=res.data['response'] 
      }
      )
      .catch(err => {
        dispatch(
          returnErrors(err.response.data, err.response.status, "PAYMENT FAILED")
        );
        console.log(err)
      });

  };


  export const verifyPayment = () => dispatch => {
    // dispatch(setUsersLoading());
    axios
      .get('/api/paystack/callback')
      .then(res =>{
        dispatch({
          type: PAYMENT_VERIFIED,
          payload: res.data
      })

        console.log(res.data)
      }
      )

      // .then(
      //   dispatch(setUsersDoneLoading())
      // )
      
      .catch(err =>
        dispatch(returnErrors(err.response.data, err.response.status))
      );
  };
  



