import {
    USER_LOADING, 
    PROFILE_LOADED
 } from "../action/types"
 import axios from "axios"

 const initialState ={
     isLoading:false,
     user:[],
     profile:[]
 }



 export default function(state =initialState, action){
    
    switch (action.type) {
        
            case PROFILE_LOADED:
                return{
                    ...state,
                    isLoading:false,
                    profile:action.payload
                }


                
        case USER_LOADING:
            return {
                ...state, 
                isLoading:true
            };

            
        default:
            return state;
    }
 }