import {
  CREATING_CAMPAIGN,
  CAMPAIGN_CREATED,
  CREATE_CAMPAIGN_FAILED,
  GETTING_SINGLE_CAMPAIGN,
  GET_ALL_CAMPAIGN,
  GET_SINGLE_CAMPAIGN,
  GETTING_ALL_CAMPAIGN,
  DELETED_CAMPAIGN,
  UPDATE_CAMPAIGN,
  GET_CATEGORIES,
  GET_RECENT_CAMPAIGNS,
  GET_ORGANISATION_CAMPAIGNS,
  CAMPAIGN_UPDATED
} from "../action/types";
import axios from "axios";

const initialState = {
  //  token:localStorage.getItem("token"),
  //  isAuthenticated:null,
  isLoading: false,
  campaign: [],
  camp: [],
  categories: [],
  recentcampaigns:[],
  orgCampaigns:[],
  singleCampaign:'',
  type : ''
};

export default function (state = initialState, action) {
  switch (action.type) {
    case CREATING_CAMPAIGN:
      return {
        ...state,
        isLoading: true,
      };
    case CAMPAIGN_CREATED:

      // alert(
      
      //   "Campaign Created Successfully"
      // )
      return {
        ...state,
        // isAuthenticated:true,
        isLoading: false,
        type : action.type,
        campaign: action.payload
        
        // camp: [action.payload, ...state.camp],

      };

      case CAMPAIGN_UPDATED:

      alert(
      
        "Campaign Updated Successfully"
      )
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          campaign: action.payload,  
        };
    case CREATE_CAMPAIGN_FAILED:
      // localStorage.removeItem("token")

      // alert("Creating Campaign Failed")
      return {
        ...state,
        // token:null,
        campaign: null,
        // isAuthenticated:false,
        isLoading: false,
      };

    // WHEN THE USER GETS A SINGPLE CAMPAIGN

    case GETTING_SINGLE_CAMPAIGN:
      return {
        ...state,
        isLoading: true,
      };

    case GET_SINGLE_CAMPAIGN:
      console.log("ACTION PAYLOAD", action.payload);
      return {
        ...state,
        // isAuthenticated:true,
        isLoading: false,
        camp: action.payload,
        singleCampaign:action.payload
        // campaign : action.payload
      };

      case GET_ALL_CAMPAIGN:
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          campaign: action.payload
          // camp : action.payload
        };
  

    // WHEN THE USER IS REQUESTING FOR A SINGLE CAMPAIGN
    case GETTING_ALL_CAMPAIGN:
        return {
          ...state,
          isLoading: true,
        };
        

      case UPDATE_CAMPAIGN:
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          campaign: action.payload,
          camp : action.payload
        };

    // WHEN THE USER IS REQUESTING FOR ALL THE CAMPAIGNS
   

    case DELETED_CAMPAIGN:
      return {
        // campaign: state.campaign.filter((item) => item._id !== action.payload),
        // camp: state.camp.filter((item) => item._id !== action.payload),
        isLoading: false,
      };


      case GET_CATEGORIES:
        return {
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            categories : action.payload
        };

        case GET_RECENT_CAMPAIGNS:
          return{
            ...state,
            // isAuthenticated:true,
            isLoading: false,
            recentcampaigns : action.payload
          }
    // WHEN THE USER GETS ALL CAMPAIGNS

          // PARTICULAR ORGANSATION CAMPAIGNS
          case GET_ORGANISATION_CAMPAIGNS:
            return{
              ...state,
              // isAuthenticated:true,
              isLoading: false,
              orgCampaigns : action.payload
  
            }

    default:
      return state;
  }
}
