import React, { Component } from 'react';
import {getRecentCampaigns} from "../../action/campaignActions"
import {connect} from 'react-redux'

import { 
    Link,
  withRouter,
 } from "react-router-dom"
 
 import store from "../../store"

class TrendingProjects extends Component {
    componentDidMount(){
        //  this.props.getAllCampaign()
        store.dispatch(getRecentCampaigns())
    
    
        // await sleep(3000)
        console.log("CATEGORIES:",this.props.campaigns.recentcampaigns)
        // console.log("PROPS:",this.props.getAllCampaign)
    }


    render() {

            return(
                <section className="trending_items">
                
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="section_tittle text-center">
                                <h2>FEATURED PROJECTS</h2>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                    {this.props.campaigns.recentcampaigns.map((campaigns, key)=> {
                        return(
                        <div className="col-lg-4 col-sm-6" key={campaigns._id}>
                        <div className="single_product_item">
                            <img 
                                src={`${campaigns.image_slide_1}`} 
                                alt="#" 
                                className="img-fluid" 
                                
                                />
                            <h3> <Link to = {`/campaigns/${campaigns._id}`}>
                                     {campaigns.project_name}
                                    </Link> </h3>
                            <p>                   
                                ₦ {campaigns.project_current_balance} of  ₦ {campaigns.project_goal} raised
                            </p>
                            <div className="percentage">
								<div className="progress">
                                    <div 
                                        className="progress-bar color-8" 
                                        role="progressbar" 
                                        style={{
                                                width:`${(campaigns.project_current_balance/campaigns.project_goal) * 100}%`, 
                                                // ,
                                                backgroundColor:"#B08EAD"
                                            }}
                                        aria-valuenow={campaigns.project_current_balance} 
                                        aria-valuemin="0" 
                                        aria-valuemax={campaigns.project_goal} >

                                        </div>
								</div>
							</div>   
                        </div>
                    </div>
                        )
                    }) }


                    </div>
                </div>
            </section>     
        );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaigns:state.campaign
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{getRecentCampaigns})(
    withRouter(TrendingProjects)
  );
// export default 
