import React, { Component } from 'react';

import {getRecentCampaigns} from  "../../../action/campaignActions"
import {connect} from 'react-redux'

import { 
  withRouter,
 } from "react-router-dom"
 import store from '../../../store'


class RecentCampaigns extends Component {

    componentDidMount(){
        //  this.props.getAllCampaign()
        store.dispatch(getRecentCampaigns())


        // await sleep(3000)
        console.log("CATEGORIES:",this.props.campaigns.recentcampaigns)
        // console.log("PROPS:",this.props.getAllCampaign)
    }
    render() {
        // const campaigns =
        return (
            <aside className="single_sidebar_widget popular_post_widget" >
            <h3 className="widget_title">Recent Campaigns</h3>
            {this.props.campaigns.recentcampaigns.map((campaigns, key)=> {
                return(
            <div className="media post_item" key={campaigns._id}>
                        <img src="/new/img/post/post_1.png" alt="post" />
                        <div className="media-body">
                            <a href="#">
                <h3>{campaigns.project_name}</h3>
                            </a>
                            <p>January 12, 2019</p>
                        </div>
                    </div>
                )
            })}
            
            
        </aside>

        );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaigns:state.campaign
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{getRecentCampaigns})(
    withRouter(RecentCampaigns)
  );