import {
    GET_SINGLE_DASHBOARD_CAMPAIGN,
    GETTING_SINGLE_CAMPAIGN
  } from "../action/types";
  import axios from "axios";
  
  const initialState = {
    //  token:localStorage.getItem("token"),
    //  isAuthenticated:null,
    isLoading: false,
    campaign: [],
    camp: [],
    dashboardsingleCampaign:''
  };
  
  export default function (state = initialState, action) {
    switch (action.type) {
  
      case GET_SINGLE_DASHBOARD_CAMPAIGN:
        console.log("ACTION PAYLOAD", action.payload);
        return {
          ...state,
          // isAuthenticated:true,
          isLoading: false,
          dashboardsingleCampaign:action.payload
        };
        case GETTING_SINGLE_CAMPAIGN:
            return {
              ...state,
              isLoading: true,
            };  
      default:
        return state;
    }
  }
  