import axios from "axios";
import { returnErrors } from "./errorActions";
import { 
      USERS_LOADED,
      REGISTER_FAIL, 
      USER_LOADED,
      USERS_LOADING, 
      UPDATING_USER,
      UPDATED_USER, 
      REGISTER_SUCCESS, 
      ORGANISATION_PROFILE_LOADED,
      GET_CATEGORIES,PROFILE_LOADED } from "./types";


import {tokenConfig} from './authAction'


var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}


// Create Profile
export const createProfile = (userID, fd, history) => dispatch => {
    // Headers
    const config = {
      headers: {
        // 'Accept': 'application/json',
        'Content-Type': 'multipart/form-data',
      }
    };
    // console.log(fd.get("profilePicture"))
  
      axios.put(`/api/signupuser/user/${userID}/profile`, fd, config)
      .then(res => {
        dispatch({
          type: USERS_LOADED,
          payload: res.data
        })
        // console.log(res.data)
        history.push('/dashboard')
        // window.location.href = "http://localhost:3000/dashboard"        
      }
        )
      .catch(err => {
        dispatch(
          returnErrors(err.response.data, err.response.status, 'UPDATE PROFILE FAILED')
        );
        // console.log(err)
        dispatch({
          type: REGISTER_FAIL
        });
      });
  };





export const getProfile = (id) => dispatch => {
  // Headers
  const config = {
    headers: {
      // 'Accept': 'application/json',
      'Content-Type': 'application/json',
    }
  };

  dispatch({
    type: USERS_LOADING
  })
    axios.get(`/api/signupuser/user/${id}/profile`, config)


    .then(res => {
      dispatch({
        type: PROFILE_LOADED,
        payload: res.data
      })
      // console.log(res.data)
      // window.location.href = "http://localhost:3000/dashboard"        
    }
      )
    .catch(err => {
      dispatch(
        returnErrors(err.response.data, err.response.status, 'PROFILE GET FAILED')
      );
      // console.log(err.response.data)
      dispatch({
        type: REGISTER_FAIL
      });
    });
};






