import React, { Component } from 'react';
import Fragment from "../../../hoc/Fragment"
import "./single.css"

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {
        getSingleDashboardCampaign} from "../../../action/campaignActions"
import store from "../../../store"



class SingleProject extends Component {
    componentDidMount(){
        console.log("IN THE PAGE RN")
        console.log(this.props)
        if(this.props.match.params.id){
           console.log(this.props.match.params.id)
           store.dispatch(getSingleDashboardCampaign(this.props.match.params.id))
        }
     }


    render() {
        const {dashboardsingleCampaign} = this.props.dashboard
        return (
            <Fragment>

    <div class="row">
      <div class="col-md-8 offset-md-2 ">
        <div class="card card-elegant" width="70%">
          <img 
                class="card-img-top" 
                src={`${dashboardsingleCampaign.image_slide_1}`} 
                width="100%" alt="Card image cap" style={{opacity:".5"}} />
          <div class="card-block">
            <h5 class="card-title text-uppercase">
                <small>{dashboardsingleCampaign.category}</small> <br />
                {dashboardsingleCampaign.project_name}
                </h5>
            <p class="card-text text-justify">
            {dashboardsingleCampaign.project_description}
            </p>

            <p class="card-text text-justify">Amount Goal:            ₦ {dashboardsingleCampaign.project_goal} </p>
            <p  class="card-text text-justify">Amount Raised:₦ {dashboardsingleCampaign.project_current_balance} </p>
            <p class="card-text text-justify">Date Project Was Created:{dashboardsingleCampaign.date_created} </p>
            <p class="card-text text-justify"> Project Deadline       {
              // dashboardsingleCampaign.project_deadline
            new Date(dashboardsingleCampaign.project_deadline).toLocaleString()
            }</p>
            
            {/* <a href="#" class="btn btn-link text-uppercase">Read More</a> */}
          </div>
        </div>
      </div>
    </div>
            </Fragment>            
        );
    }
}



const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    campaign:state.campaign,
    dashboard:state.dashboard
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{})(
    withRouter(SingleProject)
  );