import React, { Component } from 'react';
import Header from  "./Header"
import Fragment from "../../hoc/Fragment"
import Footer from "./Footer"
import Lottie from "react-lottie";

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {createFeedback} from "../../action/feedbackAction"

import * as contactUs from "../../loaders/contactUs.json"

const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: contactUs.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

class Contact extends Component {
    
    handleSubmit = async e =>{
        e.preventDefault()

        const email = this.email.value
        const firstName = this.firstName.value
        const lastName = this.lastName.value
        const text = this.text.value

        const newFeedback = {
            email:email,
            firstName:firstName, 
            lastName:lastName,
            text:text
        }
        // console.log("details ", newUser)

      await  this.props.createFeedback(newFeedback)
    }

    render() {
        return (
            <Fragment>
                <Header />




                <section class="contact-section section_padding">

                    <div class="container">




                         
          <Lottie
                        options={defaultOptions}
                        height={"50%"}
                        width={"50%"}
                      />
                    <div class="row">
        <div class="col-12">
          <h2 class="contact-title">Get in Touch</h2>
       
        </div>
        <div class="col-lg-8">
          <form class="form-contact contact_form"  id="contactForm"
            novalidate="novalidate"
            onSubmit={this.handleSubmit}
            >
            <div class="row">
              <div class="col-12">
                <div class="form-group">

                  <textarea 
                    ref={(input)=> this.text =input}

                  class="form-control w-100" name="message" id="message" cols="30" rows="9"
                    onfocus="this.placeholder = ''" onblur="this.placeholder = 'Enter Message'"
                    placeholder='Enter Message'></textarea>
                </div>
              </div>
              <div class="col-sm-6">
                <div class="form-group">
                  <input 
                        class="form-control" 
                        name="name" 
                        id="name" 
                        type="text" 
                         placeholder='Enter your first  name' 
                         ref={(input)=> this.firstName =input}

                         />
                </div>
              </div>
              <div class="col-sm-6">
                <div class="form-group">
                  <input 
                        class="form-control" 
                        name="name" 
                        id="name" 
                        type="text" 
                         placeholder='Enter your last name' 
                         ref={(input)=> this.lastName =input}

                         />
                </div>
              </div>
              <div class="col-sm-6">
                <div class="form-group">
                  <input 
                    class="form-control" 
                    name="email" 
                    id="email" 
                    type="email"
                     onfocus="this.placeholder = ''"
                    onblur="this.placeholder = 'Enter email address'"
                     placeholder='Enter email address' 
                     ref={(input)=> this.email =input}
                     />
                </div>
              </div>
            </div>
            <div class="form-group mt-3">
              <button type="submit" class="btn_3 button-contactForm">Send Message</button>
            </div>
          </form>
        </div>
        <div class="col-lg-4">
          <div class="media contact-info">
            <span class="contact-info__icon"><i class="ti-home"></i></span>
            <div class="media-body">
              <h3>Rayfield</h3>
              <p>Jos, Plateau State</p>
            </div>
          </div>
          <div class="media contact-info">
            <span class="contact-info__icon"><i class="ti-tablet"></i></span>
            <div class="media-body">
              <h3>09018916522</h3>
              <p>Mon to Fri 9am to 6pm</p>
            </div>
          </div>
          <div class="media contact-info">
            <span class="contact-info__icon"><i class="ti-email"></i></span>
            <div class="media-body">
              <h3>info@elesarr.com</h3>
              <p>Send us your query anytime!</p>
            </div>
          </div>
        </div>
      </div>

      
                    </div>
                </section>
                <Footer />
            </Fragment>
        );
    }
}


const mapStateToProps = state => ({
    auth: state.auth,
    error: state.error,
    feedback:state.feedback
  });
  
  // export default ProjectForm
  export default connect(mapStateToProps,{createFeedback})(
    withRouter(Contact)
  );
  // export default 