import axios from "axios";
import { returnErrors } from "./errorActions";
import {
//   USER_LOADING,
//   USER_LOADED,
FEEDBACK_CREATED,
FEEDBACK_CREATING,
FEEDBACK_FAIL
} from "./types";

var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}


export const createFeedback = ({email, firstName, lastName, text}) => dispatch => {

    dispatch({
        type:  FEEDBACK_CREATING
      });
    
    const config ={
        headers:{
            'Content-Type':'application/json'
        }
    };


    // REQUEST BODY
    const body = JSON.stringify({email, firstName, lastName, text})
    axios
        .post(`/api/feedback`, body, config)
        .then(res =>{
          dispatch({
            type: FEEDBACK_CREATED,
            payload: res.data
          })

          window.location.href="/"
        }
          // console.log("this is the res ", res)
        )
                .catch(err => {
                    dispatch(
                        returnErrors(err.response.data, err.response.status)
                    )
                    dispatch({
                        type:FEEDBACK_FAIL
                    })
                })
}
