import {
    GET_USERS,
    USERS_LOADING,
    PAYMENT_LOADING,
    PAYMENT_LOADED,
    PAYMENT_VERIFYING
    // USERS_LOADED
  } from "../action/types"



const initialState = {
    payment: [],
    loading: false
};


export default function(state = initialState, action) {
    switch (action.type) {
      case PAYMENT_LOADED:
        //   case USERS_LOADED:

        console.log("payload ", action.payload)
        return {
          ...state,
          payment: action.payload,
          loading: false
        };
    //   case DELETE_ITEM:
    //     return {
    //       ...state,
    //       items: state.items.filter(item => item._id !== action.payload)
    //     };
      case PAYMENT_VERIFYING:
        return {
          ...state,
        //   items: [action.payload, ...state.items]
            loading : true
        };
      case PAYMENT_LOADING:
        return {
          ...state,
          loading: true
        };
      default:
        return state;
    }
  }
